
function setAttribute(dom, key, value = '') {

  // Â¶ÇÊûúkeyÊòØclassName
  const isClassName = key === 'className'
  // Â¶ÇÊûúkeyÊòØ‰∫ã‰ª∂Âπ∂‰∏îÊòØÂáΩÊï∞
  const isEvent = /on\w+/.test(key) && typeof value === 'function'
  // Â¶ÇÊûúkeyÊòØÊ†∑Âºèstyle
  const isStyle = key === 'style'

  // ËΩ¨Êç¢‰∏∫class
  if (isClassName) {
    key = 'class'
  }

  // ËÆæÁΩÆ‰∫ã‰ª∂
  if (isEvent) {
    key = key.toLowerCase()
    dom[key] = value
  }

  // ËÆæÁΩÆÊ†∑Âºè
  if (isStyle) {
    if (typeof value === 'object') {
      const styleObject = value
      Object.keys(styleObject).forEach((key) => {
        let val = styleObject[key]
        if (typeof val === 'number') {
          val = val + 'px'
        }
        dom.style[key] = val
      })
      return
    }
    dom.style.cssText = value
  }

  // Â¶ÇÊûúÈÉΩ‰∏çÊòØÔºåÂàôÁõ¥Êé•ËÆæÁΩÆÂ±ûÊÄß
  if (!isClassName && !isEvent) {

    if (value) {
      dom.setAttribute(key, value)
    }

    if (!value) {
      dom.setAttribute(key)
    }
  }

}

export default setAttribute