import setAttribute from './setAttribute'

function render(vnode, container) {

  // Ê£ÄÊü•vnodeÁöÑÂÄºÔºåÂ¶ÇÊûúÊòØÁ©∫Âàô‰ªÄ‰πàÈÉΩ‰∏çÂÅö
  if (vnode === undefined || vnode === null || vnode === '') {
    return
  }

  // Â¶ÇÊûúvnodeÁ±ªÂûãÊòØstringÔºåÂàôÂàõÂª∫ÊñáÊú¨ËäÇÁÇπÁõ¥Êé•ÊèíÂÖ•container
  if (typeof vnode === 'string') {
    const textNode = document.createTextNode(vnode)
    return container.appendChild(textNode)
  }

  const { tag, attrs, childrens } = vnode
  const childrensIsArray = Array.isArray(childrens)

  let dom = document.createElement(tag)

  // Ê£ÄÊü•attrsÔºåÈÅçÂéÜËØ•ÂØπË±°ÔºåÂπ∂‰ΩøÁî®setAttributeÊñπÊ≥ïËÆæÁΩÆdomÂ±ûÊÄß
  if (attrs) {
    Object.keys(attrs).forEach((key) => {
      const value = attrs[key]
      setAttribute(dom, key, value)
    })
  }

  if (childrensIsArray && childrens.length) {
    childrens.forEach((item) => {
      render(item, dom)
    })
  }

  container.appendChild(dom)
  console.log(dom)
}

export default {
  render
}